<template>
    <el-container class="container product" v-loading="loading" :element-loading-text="loadingText">
        <el-form :model="formData" :rules="rules" ref="form" label-width="100px">
            <el-form-item label="" prop="pdNm">
                <el-input v-model="formData.pdNm" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="avlSt">
                <el-select size="small" class="my-select" placeholder="" v-model="formData.avlSt">
                    <el-option v-for="item in avlStOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="pdBlngSbj">
                <el-select @change="selectPdBlngSbj" size="small" class="my-select" placeholder="" v-model="formData.pdBlngSbj">
                    <el-option v-for="item in pdBlngSbjOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="pdTpOption" v-if="formData.pdBlngSbj">
                <el-tree :data="pdTpOption" :props="defaultProps" accordion>
                    <span slot-scope="{node, data}">
                        <el-checkbox v-model="data.checked" @change="checkoutChange(data)" :label="data.label" v-if="!data.children"></el-checkbox>
                        <span v-if="data.children">{{data.label}}</span>
                    </span>
                </el-tree>
            </el-form-item>
            <el-form-item label="" prop="pdDispNo">
                <el-input v-model="formData.pdDispNo" size="small" placeholder="0~999"></el-input>
            </el-form-item>
            <el-form-item label="" prop="pdBrf">
                <el-input v-model="formData.pdBrf" type="textarea" size="small" placeholder="300"></el-input>
            </el-form-item>
            <el-form-item label="LOGO" prop="pdLogoSrc">
                <el-upload class="pd-logo-uploader" :action="logoAction" :show-file-list="false" :limit="1"
                    :http-request="myLogoUpload" :before-upload="beforeLogoUpload">
                    <img v-if="formData.pdLogoSrc" :src="formData.pdLogoSrc" class="pd-logo">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="" prop="pdUrl">
                <el-upload :action="proAction" :file-list="proList" :before-upload="beforePdUpload" :limit="1"
                    :http-request="myProUpload" :on-remove="removeFile">
                    <el-button type="primary"></el-button>
                </el-upload>
                <a :href="proShowUrl" v-if="proShowUrl" target="_blank"></a>
            </el-form-item>
        </el-form>
    </el-container>
</template>

<script>
    import {
        productMaintenance
    } from '@/api'
    import {
        getPdTp,
        myUpload
    } from '@/utils/myFn';
    import {
        filePath
    } from '@/config'
    export default {
        data() {
            return {
                formData: {
                    pdNm: "",
                    pdBlngSbj: "",
                    pdTp: '',
                    pdDispNo: '',
                    pdBrf: '',
                    pdLogoSrc: '',
                    pdUrl: '',
                    avlSt: '0'
                },
                rules: {
                    pdNm: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    pdDispNo: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    pdBrf: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }]
                },
                proList: [],
                pdBlngSbjOption: [{
                        value: "1",
                        label: ""
                    },
                    {
                        value: "2",
                        label: ""
                    }
                ],
                avlStOption: [{
                    value: '0',
                    label: ''
                }, {
                    value: '1',
                    label: ''
                }],
                pdTpOption: [],
                defaultProps: {
                    children: "children",
                    label: "label"
                },
                addOrEdit: true,
                logoAction: `/upload?savePath=productLogo/${sessionStorage.userId}/`,
                proAction: `/upload?savePath=productFile/${sessionStorage.userId}/`,
                proFileName: '',
                proShowUrl: '',
                loading: false,
                loadingText: "...",
            };
        },
        methods: {

            //logo 
            beforeLogoUpload(file) {
                const {
                    type,
                    size
                } = file
                if (!type.includes('png') && !type.includes('jpeg')) {
                    this.$message.warning('logojpgpng')
                    return false
                }
                if (Math.ceil(size / 1024 / 1024) > 2) {
                    this.$message.warning(' 2M')
                    return false
                }
                return true
            },

            // 
            beforePdUpload(file) {
                const {
                    type,
                    name
                } = file
                if (!type.includes('html') && !type.includes('pdf')) {
                    this.$message.warning('pdfhtml')
                    return false
                }
                this.proFileName = name
                return true
            },

            removeFile() {
                this.formData.pdUrl = ''
                this.proList = []
            },

            //logo 
            async myLogoUpload(content) {
                try {
                    const res = await myUpload(content, this.logoAction)
                    console.log(res)
                    const {
                        path
                    } = res.data
                    const {origin} = window.location
                    this.formData.pdLogoSrc = `${origin}/download?filePath=${filePath}${path.replace(/\\/g, '/')}`
                    this.$message.success('logo')
                } catch (err) {
                    console.log(err)
                    this.$message.error('logo')
                }
            },

            //
            async myProUpload(content) {
                try {
                    const res = await myUpload(content, this.proAction)
                    console.log(res)
                    const {
                        path,
                        file
                    } = res.data
                    this.formData.pdUrl = path
                    const {origin} = window.location
                    this.proList = [{
                        name: this.proFileName ? this.proFileName : '',
                        url: `${origin}/download?filePath=${filePath}${path.replace(/\\/g, '/')}`
                    }]
                    this.proShowUrl = `${origin}/download?filePath=${filePath}${path.replace(/\\/g, '/')}`
                    this.$message.success('')
                } catch (err) {
                    console.log(err)
                    this.$message.error('')
                }
            },

            //
            submit() {
                this.addOrEdit ? this.add() : this.edit()
            },

            //
            async add() {
                try {
                    await this.validate()
                } catch {
                    return
                }
                try {
                    const res = await this.$http({
                        method: 'post',
                        url: productMaintenance.add,
                        data: this.formData
                    })
                    const {
                        sysTxStatus,
                        sysRespDesc
                    } = res.data
                    if (sysTxStatus === '00') {
                        this.$message.success('')
                        this.$emit('getData')
                    } else {
                        this.$message.warning(sysRespDesc)
                    }
                } catch (err) {
                    if (err) console.log(err)
                    this.$message.error('')
                }
            },

            //
            async edit() {
                try {
                    await this.validate()
                } catch {
                    return
                }
                const data = Object.assign({}, this.formData)
                const pdBlngSbj = this.pdBlngSbjOption.filter(item => {
                    return item.label === data.pdBlngSbj
                })
                data.pdBlngSbj = pdBlngSbj[0] ? pdBlngSbj[0].value : ''
                try {
                    const res = await this.$http({
                        method: 'patch',
                        url: productMaintenance.edit(this.formData.pdId),
                        data
                    })
                    const {
                        sysTxStatus,
                        sysRespDesc
                    } = res.data
                    if (sysTxStatus === '00') {
                        this.$message.success('')
                        this.$emit('getData')
                    } else {
                        this.$message.warning(sysRespDesc)
                    }
                } catch (err) {
                    console.log(err)
                    this.$message.error('')
                }
            },

            //
            validate() {
                return new Promise((resolve, reject) => {
                    this.$refs['form'].validate(valid => {
                        valid ? resolve() : reject()
                    })
                })
            },

            //
            checkoutChange(data) {
                const checked = data.checked
                this.changePdTp(this.pdTpOption)
                data.checked = checked
                this.formData.pdTp = checked ? data.id : ''
            },

            //checked  false
            changePdTp(arr) {
                arr.forEach(item => {
                    if (item.children) return this.changePdTp(item.children)
                    item.checked = false
                })
            },

            //
            async selectPdBlngSbj() {
                if (!this.formData.pdBlngSbj) return
                try {
                    this.loading = true
                    this.pdTpOption = await getPdTp(this.formData.pdBlngSbj, {
                        scope: 1,
                        tpId: '0'
                    })
                    this.loading = false
                } catch (err) {
                    console.log(err)
                    this.loading = false
                    this.$message.error('')
                }
            },

            // 
            initAdd() {
                this.addOrEdit = true
                for (let k in this.formData) {
                    if (k === 'avlSt') {
                        this.formData[k] = '0'
                    } else {
                        this.formData[k] = ''
                    }
                }
                this.proShowUrl = ''
            },

            // 
            async initEdit(row) {
                this.addOrEdit = false
                if (row.pdBlngSbj) {
                    this.formData.pdBlngSbj = row.pdBlngSbj === '' ? '1' : '2'
                    await this.selectPdBlngSbj()
                    this.setChecked(row.pdTp)
                }
                this.formData = Object.assign({}, row)
                const {origin} = window.location
                this.proShowUrl = this.formData.pdUrl ?  `${origin}/download?filePath=${filePath}${this.formData.pdUrl.replace(/\\/g, '/')}`: ''
            },

            setChecked(id) {
                this.forEachPdTpOption(this.pdTpOption, id)
            },

            forEachPdTpOption(arr, id) {
                for (let val of arr) {
                    if (val.children) {
                        this.forEachPdTpOption(val.children, id)
                    } else {
                        if (val.id === id) {
                            val.checked = true
                            break
                        }
                    }
                }
            }
        }
    };
</script>

<style lang="scss" scoped>
    .container {
        width: 100%;
        height: 100%;

        .section {
            display: block;
            margin-right: 20px;
        }

        .my-input {
            width: 150px;
            height: 32px;
        }

        .my-select {
            width: 150px;
            height: 32px;
        }
    }
</style>
<style lang="scss">
    .pd-logo-uploader .el-upload {
        border: 1px solid #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .pd-logo-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .pd-logo {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>